<template>
  <v-container
    fluid
  >
    <v-toolbar
      flat
    >
      <v-toolbar-title>Data Aturan</v-toolbar-title>
      <v-spacer/>
      <v-toolbar-items>
        <v-btn
          flat
          @click="$router.push('/app/aturan/add')"
        >
          <font-awesome-icon
            icon="plus"
            size="2x"
          ></font-awesome-icon>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-layout row>
      <v-flex v-for="(desc, idx) in descriptions"
        md3
        :key="`desc-${idx}`"
      >
        <v-card flat tile>
          <v-card-text class="text-xs-center">
            <div class="title ev-russo" :class="`${desc.color}--text`">{{ desc.count }}</div>
            <div class="subtitle">{{ desc.text }}</div>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>

    <v-data-table
      :headers="tableHeaders"
      :items="items"
      class="my-3"
    >
      <template
        v-slot:items="props"
      >
        <td>
          {{ props.item.display.produksi }}
        </td>
        <td>
          {{ props.item.display.penjualan }}
        </td>
        <td>
          {{ props.item.display.persediaan }}
        </td>
        <td class="font-weight-bold">
          {{ props.item.display.permintaan }}
        </td>
        <td>
          <v-menu>
            <template v-slot:activator="{ on }">
              <font-awesome-icon
                v-on="on"
                icon="ellipsis-h"
                size="2x"
                class="grey--text"></font-awesome-icon>
            </template>
            <v-list dense>
              <v-list-tile :to="`/app/aturan/${props.item.id}/edit`">
                <v-list-tile-sub-title>
                  Update
                </v-list-tile-sub-title>
              </v-list-tile>
              <v-list-tile @click="deleteData(props.item.id)">
                <v-list-tile-sub-title>
                  Delete
                </v-list-tile-sub-title>
              </v-list-tile>
            </v-list>
          </v-menu>
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import Vue from "vue";
import tableHeaders from "./tableHeaders";
import RuleAPI from "@/services/rule";
import evbus from "@/evbus";

const _descriptions = () => (
  [
    {
      count: 120,
      text: "Total Data",
      color: "green"
    },
    {
      count: 34,
      text: "Rendah",
      color: "blue"
    },
    {
      count: 45,
      text: "Sedang",
      color: "indigo"
    },
    {
      count: 28,
      text: "Tinggi",
      color: "brown"
    }
  ]
)

export default Vue.extend({
  name: "AturanList",
  data: () => ({
    descriptions: _descriptions(),
    tableHeaders,
    items: []
  }),
  methods: {
    async loadData() {
      evbus.$emit("loading-on");
      try {
        const data = await RuleAPI.find();
        this.items = data.items;

        let counts = data.counts;
        let descs = _descriptions();
        descs[0].count = counts[0];
        descs[1].count = counts[1];
        descs[2].count = counts[2];
        descs[3].count = counts[3];

        this.descriptions = descs;
      } catch (err) {
        console.log(err);
      }
      evbus.$emit("loading-off");
    },
    deleteData(id) {
      const message = "Apakah anda yakin akan menghapus aturan #" + id;
      const action = async () => {
        try {
          await RuleAPI.delete(id);
          evbus.$emit("success", "Sukses menghapus data");
        } catch (err) {
          evbus.$emit("error", "Gagal menghapus data");
          console.log(err);
        } finally {
          evbus.$emit("loading-off");
          this.loadData();
        }
      };
      evbus.$emit("app-action", { message, action });
    }
  },
  mounted() {
    this.loadData();
  }
});
</script>

